define(["exports", "../../lib/opt/marked.min"], function(exports,marked) {
	var CommentBox = React.createClass({
	  loadCommentsFromServer: function() {
	    $.ajax({
	      url: this.props.url,
	      dataType: 'json',
	      cache: false,
	      success: function(data) {
	        this.setState({data: data});
	      }.bind(this),
	      error: function(xhr, status, err) {
	        console.error(this.props.url, status, err.toString());
	      }.bind(this)
	    });
	  },		
	  getInitialState: function() {
	    return {data: []};
	  },
	  componentDidMount: function() {
	    this.loadCommentsFromServer();
    	setInterval(this.loadCommentsFromServer, this.props.pollInterval);
	  },
	  handleCommentSubmit: function(comment) {
		var comments = this.state.data;
	    // Optimistically set an id on the new comment. It will be replaced by an
	    // id generated by the server. In a production application you would likely
	    // not use Date.now() for this and would have a more robust system in place.
    	comment.id = Date.now();
    	var newComments = comments.concat([comment]);
    	this.setState({data: newComments});

	    $.ajax({
	      url: this.props.url,
	      dataType: 'JSON',
	      type: 'POST',
	      data: comment,
	      success: function(data) {
	        //this.setState({data: data});
	      }.bind(this),
	      error: function(xhr, status, err) {
	      	//this.setState({data: comments});
	        console.error(this.props.url, status, err.toString());
	      }.bind(this)
	    });
	  },
	  handleCommentDelete: function(comment) {
		var comments = this.state.data;
    	var newComments = [];
    	for (var i = 0; i < comments.length; i++) {
    		if(comments[i].id != comment.id){
    			newComments.push(comments[i]);
    		}
    	}
    	this.setState({data: newComments});
	    $.ajax({
	      url: this.props.url+"/"+comment.id,
	      dataType: 'json',
	      type: 'DELETE',
	      success: function(data) {
	        //this.setState({data: data});
	      }.bind(this),
	      error: function(xhr, status, err) {
	      	//this.setState({data: comments});
	        console.error(this.props.url, status, err.toString());
	      }.bind(this)
	    });
	  },
	  handleCommentUpdate: function(comment) {
		var comments = this.state.data;
    	for (var i = 0; i < comments.length; i++) {
    		if(comments[i].id == comment.id){
    			comments[i].text = comment.text;
    		}
    	}
    	this.setState({data: comments});
	    $.ajax({
	      url: this.props.url+"/"+comment.id,
	      dataType: 'json',
	      type: 'POST',
	      data: comment,
	      success: function(data) {
	        //this.setState({data: data});
	      }.bind(this),
	      error: function(xhr, status, err) {
	      	//this.setState({data: comments});
	        console.error(this.props.url, status, err.toString());
	      }.bind(this)
	    });    	
	  },	  	  
	  render: function() {
	    return (
	      <div className="commentBox">
	        <h1>Comments</h1>
	        <CommentForm onCommentSubmit={this.handleCommentSubmit} />
	        <CommentList data={this.state.data} 
	        	onCommentDelete={this.handleCommentDelete}
	        	onCommentUpdate={this.handleCommentUpdate}/>
	      </div>
	    );
	  }
	});


	var CommentList = React.createClass({
	  render: function() {
	    var commentNodes = this.props.data.map(function(comment) {
	      return (
	        <Comment author={comment.author} key={comment.id} id={comment.id} text={comment.text}
	        	onCommentDelete={this.props.onCommentDelete} 
	        	onCommentUpdate={this.props.onCommentUpdate}>
	        </Comment>
	      );
	    },this);

	    return (
	      <div className="commentList">
	        {commentNodes}
	      </div>
	    );
	  }
	});	


	var Comment = React.createClass({
	  getInitialState: function() {
	    return {text: ''};
	  },
	  rawMarkup: function() {
	    var rawMarkup = marked(this.props.text, {sanitize: true});
	    return { __html: rawMarkup };
	  },
	  handleDelete: function(){
	  	 this.props.onCommentDelete({id:this.props.id});
	  },
	  handleUpdate: function(){
	    var text = this.state.text.trim();
	    if (!text) {
	      return;
	    }
	  	 this.props.onCommentUpdate({id:this.props.id, text:text});
	  },
	  handleTextChange: function(e) {
	    this.setState({text: e.target.value});
	  },
	  render: function() {
	    return (
	      <div className="comment">
	        <h2 className="commentAuthor">
	          {this.props.author}
	        </h2>
	        <span dangerouslySetInnerHTML={this.rawMarkup()} />
	        <input
	          type="text"
	          placeholder={this.props.text}
	          value={this.state.text}
	          onChange={this.handleTextChange}
	        /><br/>
	        <div>
	       		<button onClick={this.handleDelete}>Delete</button>
	       		<button onClick={this.handleUpdate}>Update</button>
	       	</div>
	      </div>
	    );
	  }
	});	

	var CommentForm = React.createClass({
	  getInitialState: function() {
	    return {author: '', text: ''};
	  },
	  handleAuthorChange: function(e) {
	    this.setState({author: e.target.value});
	  },
	  handleTextChange: function(e) {
	    this.setState({text: e.target.value});
	  },
	  handleSubmit: function(e) {
	    e.preventDefault();
	    var author = this.state.author.trim();
	    var text = this.state.text.trim();
	    if (!text || !author) {
	      return;
	    }
	    this.props.onCommentSubmit({author: author, text: text});
	    this.setState({author: '', text: ''});
	  },	  
	  render: function() {
	    return (
	      <form className="commentForm" onSubmit={this.handleSubmit}>
	      	<div>
		        <input
		          type="text"
		          placeholder="Your name"
		          value={this.state.author}
		          onChange={this.handleAuthorChange}
		        />
		        <input
		          type="text"
		          placeholder="Say something..."
		          value={this.state.text}
		          onChange={this.handleTextChange}
		        />
		        <input type="submit" value="Post" />
	        </div>
	      </form>
	    );
	  }
	});	
	
	exports.CommentBox = CommentBox;
});